@import './assets/vendor/_config.scss';
@import './assets/vendor/_normalize.scss';
@import './assets/vendor/_ui.scss';

* {
  pointer-events: auto;
}

.launcher {
  &-block {
    width: 1250px;
    height: 791px;
    border-radius: 30px;
    background: radial-gradient(64.34% 48.72% at 68.36% 54.87%, #202D51 0%, #16203B 100%);
    position: relative;
    overflow: hidden;
  }
  &-empty {
    height: 479px;
    padding-left: 40px;
    align-items: center;
    display: flex;
    &__text {
      font-size: 20px;
      font-weight: 700;
    }
  }
  &-loading {
    display: flex;
    align-items: center;
    justify-content: center;
    &__spinner {
      width: 48px;
      height: 48px;
      border: 5px solid #FFF;
      border-bottom-color: #408AF8;
      border-radius: 50%;
      display: inline-block;
      box-sizing: border-box;
      animation: rotation 1s linear infinite;

      @keyframes rotation {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }
    }
  }
  &-server {
    padding: 40px 0 53px 70px;
    &__list {
      margin-top: 40px;
      gap: 20px;
      &-item {
        width: 218px;
      }
      &-card {
        padding: 30px 20px 20px 20px;
        border-radius: 20px;
        border: 1px solid rgba(255, 255, 255, 0.10);
        background: rgba(255, 255, 255, 0.05);
        backdrop-filter: blur(15px);
        width: 100%;
        position: relative;
        height: 217px;
      }
      &-soon {
        height: 100%;
        width: 100%;
      }
      &-title {
        font-size: 25px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
      }
      &-status {
        margin-top: 5px;
        gap: 10px;
      }
      &-indicator {
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background: #D0FF6B;
        &_offline {
          background: #b00000;
        }
      }
      &-icon {
        width: 15px;
        height: 15px;
        border-radius: 5px;
        background: rgba(255, 255, 255, 0.20);
      }
      &-text {
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        color: rgba(255, 255, 255, 0.5);
      }
      &-progress {
        margin-top: 20px;
      }
      &-bar {
        width: 65px;
        height: 4px;
        border-radius: 10px;
        position: relative;
        overflow: hidden;
        background: rgba(255, 255, 255, 0.21);
        &-bg {
          position: absolute;
          top: 0;
          left: 0;
          background: #3583F7;
          height: 100%;
        }
      }
      &-counter {
        font-size: 12px;
        font-style: italic;
        font-weight: 800;
        line-height: normal;
        color: rgba(255, 255, 255, 0.50);
        span {
          color: #FFF;
        }
      }
      &-bonus {
        position: absolute;
        top: 20px;
        right: 20px;
        width: 30px;
        height: 30px;
        border-radius: 10px;
        background: #F7CD3A;
        font-size: 16px;
        font-style: italic;
        font-weight: 800;
        line-height: normal;
        color: #000;
      }
      &-btn {
        width: 100%;
        margin-top: 20px;
        gap: 10px;
        font-size: 20px;
        font-style: italic;
        font-weight: 800;
        line-height: normal;
        color: #FFF;
        border-radius: 10px;
        background: linear-gradient(90deg, #2276F3 0%, #428BF9 100%);
        padding: 15.5px 0;
        svg {
          display: block;
        }
        &:disabled {
          background: rgba(255, 255, 255, 0.10);
          color: rgba(255, 255, 255, 0.20);
          //pointer-events: none;
          path {
            fill-opacity: 0.2;
          }
        }
      }
    }
    &__title {
      gap: 20px;
      &-icon {
        svg {
          display: block;
        }
      }
      &-text {
        font-size: 21px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        color: #fff;
      }
    }
  }
  &-online {
    width: 716px;
    background: linear-gradient(94deg, rgba(37, 120, 244, 0.30) 38.43%, rgba(37, 120, 244, 0.00) 90.54%);
    backdrop-filter: blur(2.5px);
    &__block {
      width: fit-content;
      padding: 18px 74px 18px 70px;
      position: relative;
      &-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        svg {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
    }
    &__content {
      position: relative;
      z-index: 1;
      &-title {
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        color: rgba(255, 255, 255, 0.5);
      }
    }
    &__count {
      margin-top: 5px;
      gap: 10px;
      &-icon {
        svg {
          display: block;
        }
      }
      &-value {
        font-size: 35px;
        font-style: italic;
        font-weight: 800;
        line-height: normal;
        text-transform: uppercase;
      }
    }
  }
  &-header {
    padding: 30px 70px;
    position: relative;
    z-index: 1;
    &__logo {
      margin-right: 100px;
      img {
        display: block;
      }
    }
    &__btns {
      gap: 10px;
    }
    &__btn {
      width: 26px;
      height: 26px;
      border-radius: 10px;
      border: 1px solid rgba(255, 255, 255, 0.20);
      svg {
        display: block;
        path, rect {
          transition: all .4s ease;
        }
      }
      &:hover {
        background: #FFF;
        border-color: #fff;
        svg path, svg rect {
          fill: #000;
          fill-opacity: 1;
        }
      }
    }
    &__nav {
      &-list {
        gap: 30px;
      }
      &-card {
        gap: 10px;
        cursor: pointer;
        position: relative;
        &:after {
          content: '';
          display: block;
          height: 2px;
          width: 100%;
          position: absolute;
          opacity: 0;
          transition: all .4s ease;
          bottom: -10px;
          left: 0;
          border-radius: 12px;
          background: #3583F7;
          box-shadow: 0px -2px 10px 0px #3583F7;
        }
        &:before {
          content: '';
          display: block;
          position: absolute;
          left: 0;
          height: 10px;
          bottom: -10px;
          width: 100%;
        }
        &:hover {
          &:after {
            opacity: 1;
          }
          .launcher-header__nav-icon path {
            fill-opacity: 1;
          }
        }
        &.active {
          &:after {
            opacity: 1;
          }
          .launcher-header__nav-icon path {
            fill-opacity: 1;
          }
        }
      }
      &-icon {
        svg {
          display: block;
          path {
            transition: all .4s ease;
          }
        }
      }
      &-text {
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        color: rgba(255, 255, 255, 0.5);
      }
    }
    &__avatar {
      width: 41px;
      height: 41px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 40px;
      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &-person {
    position: absolute;
    right: 0;
    //pointer-events: none;
    bottom: 0;
    img {
      display: block;
    }
  }
  &-download {
    padding: 0 70px;
    background: linear-gradient(180deg, #111931 0%, rgba(17, 25, 49, 0.00) 100%);
    height: 218px;
    width: 1032px;
    &__main {
      gap: 20px;
    }
    &__done {
      gap: 20px;
      &-icon {
        img {
          display: block;
        }
      }
    }
    &__btn {
      padding: 15.5px 16px;
      border-radius: 10px;
      background: #FFF;
      gap: 10px;
      font-size: 20px;
      font-style: italic;
      font-weight: 800;
      line-height: normal;
      svg {
        display: block;
        path {
          transition: all .4s ease;
        }
      }
      &:hover {
        background: #4EB7FF;
        color: #fff;
        path {
          fill: #fff;
        }
      }
    }
    &__progress {
      width: 638px;
      &-head {
        width: 100%;
      }
      &-file {
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        color: rgba(255, 255, 255, 0.5);
      }
      &-figure {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        svg {
          display: block;
        }
      }
      &-offer {
        width: 100%;
        height: 100%;
        overflow: hidden;
        border-radius: 10px;
        overflow: hidden;
        position: relative;
      }
      &-bar {
        margin-top: 20px;
        position: relative;
        height: 44px;
        //overflow: hidden;
        border-radius: 10px;
        background: rgba(255, 255, 255, 0.10);
        width: 100%;
        &-bg {
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          background: radial-gradient(308.12% 100% at 0% 50%, #2377F3 0%, #408AF8 67.09%, #FFF 100%);
        }
      }
      &-counter {
        font-size: 12px;
        font-style: italic;
        font-weight: 800;
        line-height: normal;
        color: rgba(255, 255, 255, 0.50);
        span {
          font-size: 12px;
          font-style: italic;
          font-weight: 800;
          line-height: normal;
          color: #FFF;
        }
      }
    }
    &__content {
      max-width: 310px;
      &-title {
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
      }
      &-text {
        margin-top: 5px;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
}
